@media(max-width: 1536px){
    .review{
        &__img img:first-child {
            margin-left: -530px;
        }
        &::before{
            top: 33%;
            left: 0;
        }
        &::after{
            right: -20%;
        }
    }
}
@media(max-width: 1280px){
    .review{
        &__img img {
            margin-left: -500px;
        }
        &::before {
            top: 20%;
            // left: -40%;
        }
        &::after {
            display: none;
        }
    }
    .conditions{
        &__list{
            justify-content: center;
            row-gap: 0px;
            column-gap: 15px;
        }
        &__item{
            width: 485px;
        }
    }
    .steps{
        &__item:first-child::before, &__item:nth-child(2)::before{
            width: 90%;
            right: -46%;
        }
    }
}
@media(max-width: 1024px){
    .review{
        &__inner{
            gap: 30px;
        }
        &__img img:first-child {
            display: none;
        }
        &__img img:nth-child(2) {
            display: block;
            margin-left: -150px;
            width: 550px;
            height: 367px;
        }
        &::before {
            top: -10%;
            left: -23%;
        }
    }
    .conditions{
        padding-top: 120px;
        &__item{
            width: 355px;
        }
        &__item-title{
            max-width: 225px;
            font-size: 16px;

        }
        &__item-text{
            font-size: 14px;
        }
        &__item:nth-child(even).animation-show{
            opacity: 1;
            filter: blur(0px);
            transform: translate(0, 20px);
            transition-delay: 200ms;
        }
    }
    .steps{
        &__inner{
            padding-top: 160px;
        }
        &__item-title{
            font-size: 16px;
        }
        &__item-text{
            font-size: 14px;
        }
    }
    .why{
        margin-top: 65px;
        &__list{
            max-width: 610px;
            justify-content: flex-start;
            gap: 10px;
            margin: 0 auto;
        }
        &__item{
            width: 48%;
        }
    }
    .install{
        &__title{
            margin-bottom: 15px!important;
        }
        &__text{
            margin-bottom: 30px;
        }
        &__img img{
            width: 888px;
        }
        &__img-link{
            top: 51%;
            left: 6%;
        }
        &__link{
            font-size: 16px;
            padding: 6px 40px;
            gap: 5px;
            width: 285px;
        }
    }
    .ad{
        &__inner{
            flex-direction: column-reverse;
            gap: 72px;
        }
        &__inner::before{
            left: 20%;
        }
        &__info-title{
            margin: 0 0 25px 0;
        }
        &__info-text{
            font-size: 20px;
        }
        &__info-link{
            width: 315px;
            margin-top: 30px;
        }
        &__img img{
            max-width: 100%;
        }
        &__img.animation-to-right{transform: translateY(-100%);}
        &__info.animation-to-left{transform: translateY(-100%);}
    }
    .link{
        padding: 14px 25px;
    }
    .link span {
        font-size: 13px;
    }
    .title{
        font-size: 30px;
    }
}
@media(max-width: 768px){
    .review{
        &__img img {
            margin-left: -210px;
        }
        &::before {
            left: -30%;
        }
    }
    .steps__bg-inner::after{
    }
    .conditions{
        &__item{
            width: 294px;
        }
        &__item-title{
            max-width: 185px;
        }
    }
    .install{
        padding-top: 140px;
        &__inner{
            flex-direction: column;
            gap: 0;
        }
        &__title{
            text-align: center;
        }
        &__text{
            text-align: center;
        }
        &__img img {
            margin-right: -300px;
        }
        &__link{
            margin: 0 auto;
        }
    }
    .ad{
        &__inner{
            padding-top: 120px;
        }
    }
    .warning{
        padding-top: 120px;
        &__title{
            font-size: 16px;
        }
        &__text{
            font-size: 14px;
        }
    }
    .footer{
        &__info p{
            font-size: 14px;
        }
    }
    .title{
        margin: 0 auto 35px auto;
    }
}
@media(max-width: 640px){
    .select2{
        &-container{ //block width
            width: 133px!important;
        }
        &-container &-selection--single &-selection__rendered{ //inner padding
            padding: 5px 3px 0 5px;
        }
        &-selection__rendered span{ //between flag and lang
            gap: 5px;
        }
        &-container--default &-selection--single &-selection__arrow{ //arrow    
            right: 3px;
        }
        &-container--default &-selection--single:hover{
            box-shadow: 4px 4px 5px -1px #00bb6a;
        }
    }
    .header{
        &__interface{
            gap: 5px;
        }
        .header__interface-auth{
            width: 84px;
            padding: 12px 14px;
            &:hover{
                box-shadow: 4px 4px 5px -1px #00bb6a;
            }
        }
    }
    .review{
        margin-top: 60px;
        &__inner{
            flex-direction: column-reverse;
        }
        &__info{
            max-width: 100%;
            margin-left: 0;
        }
        &__img img:nth-child(2) {
            margin-left: -60px;
        }
        &__img img:nth-child(2) .animation-hide, .animation-to-right {
            all: unset;
        }
        &__info-link{
            margin-top: 20px;
        }
        &::before {
            top: 10%;
            left: -31%;
            height: 640px;
            opacity: .25;
        }
    }
    .conditions{
        &__list{
            column-gap: 0;
            row-gap: 0;
            gap: 15px;
        }
        &__item{
            width: 294px;
            // max-height: 290px;
            margin-bottom: 0;
        }
        &__item:nth-child(even).animation-show{
            transform: translate(0);
        }
        &::before{
            right: -420px;
            top: 32%;
        }
    }
    .steps{
        &__list{
            flex-direction: column;
            align-items: center;
            gap: 127px;
        }
        &__item.animation-to-right{
            transform: translateY(-100%);
        }
        &__item:first-child::before, &__item:nth-child(2)::before{
            width: 87px;
            top: auto;
            right: auto;
            left: 50%;
            bottom: -70px;
            transform: translate(-50%, 0) rotate(90deg);
        }
        &__item-count{
            padding-top: 10px;
        }
        &__item-box{
            width: 300px;
        }
        &__bg{
            top: 85%;
        }
        &__bg-img {
            width: 100%;
            object-fit: cover;
            height: 523px;
        }
        &__bg-inner::before{
            left: -11%;
        }
    }
    .why{
        &__list{
            // max-width: 610px;
            justify-content: center;
            gap: 30px;
            margin: 0 auto;
        }
        &__item{
            width: 100%;
            text-align: center;
            margin-bottom: 0;
        }
        &__item-title{
            max-width: 100%;
            margin-bottom: 10px;
        }
        &__item-text{
            max-width: 100%;
        }
    }
    .install{
        &__img img {
            margin-right: -489px;
        }
    }
    .ad{
        &__inner{
            gap: 65px;
        }
        &__inner::before{
            left: 50%;
            transform: translateX(-50%);
        }
        &__info{
            text-align: center;
        }
        &__info-title{
            text-align: center;
        }
        &__info-text{
            text-align: center;
            font-size: 18px;
        }
        &__info-link{
            margin: 35px auto 0;
        }
    }
    .footer{
        padding-top: 50px;
    }
    .link{
        padding: 14px 25px;
    }
    .logo{
        font-size: 22px;
    }
}
